@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap');
.card-grid-container {
    font-family: 'Open Sans', sans-serif; /* Modern and clean font */
    max-width: 1200px;
    margin: 0 auto; /* Center the grid in the middle of the page */
    padding: 20px;
  }
  
  .filters {
    display: flex;
    justify-content: left;
    align-items: center;
    margin-bottom: 30px;
  }
  
  .filters > select {
    padding-top: 4px;
    padding-bottom: 4px;
    padding-left: 8px;
    padding-right: 8px;
    border-radius: 20px;
    border: 1px solid #ccc;
    font-size: 16px;
    color: #333;
    background-color: #fff;
    cursor: pointer;
  }
  
  .filters > .selected-tags {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  
  .filters > .selected-tags > .tag {
    background: #007bff;
    color: #ffffff;
    border-radius: 20px;
    padding-top: 4px;
    padding-bottom: 4px;
    padding-left: 8px;
    padding-right: 8px;
    margin: 10px 10px 10px 10px;
    margin-right: 5px;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: normal;
    cursor: default;
  }
  
  .filters > .selected-tags > .tag > button {
    background-color: transparent;
    border: none;
    color: #ffffff;
    margin-left: 5px;
    cursor: pointer;
  }
  
  .clear-filters {
    background-color: #dc3545;
    color: #ffffff;
    border-radius: 20px;
    padding-top: 4px;
    padding-bottom: 4px;
    padding-left: 8px;
    padding-right: 8px;
    margin: 10px 10px 10px 10px;
    display: flex;
    align-items: center;
    border: 0px;
    font-size: 14px;
    font-weight: normal;
    cursor: default;
  }
  
  .card-grid {
    display: flex;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    
  }
  
  @media (max-width: 992px) {
    .card-grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 576px) {
    .card-grid {
      grid-template-columns: 1fr;
    }
  
    .filters {
      flex-direction: column;
      align-items: stretch;
    }
  
    .filters > button {
      margin-top: 15px;
    }
  
    .filters > select {
      width: 100%;
    }
  }

  .personal-details {
    justify-content: space-between;
    text-align: left;
    display: flex;
  }

  .personal-details > h1 {
    font-family: 'DM Serif Display', serif;
  }

  .university {
    display: flex;
    align-items: center;
    margin-bottom: 0px;
    margin-top: 0px;
  }

  .university > img {
    height: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .education > h3, h4, h5{
    margin-top: 0px;
    margin-bottom: 0px;
    font-weight: 400;

  }

  .education {
    width : 500px;
    border-radius: 5px;
    border: 1px solid rgb(108, 107, 107);
    padding: 10px;
    background-color: #232733;
    height: max-content;
  }

  .work-experience {
    text-align: left;
    border-radius: 5px;
    margin-bottom: 10px;
  }

  .work-experience > h3 {
    margin-top: 0px;
  }

  .experiences-container {
    display: flex;
  }

  .company-name-logo {
    display: flex;
    align-items: center;
    margin-top: 3px;
    margin-bottom: 3px;
  }

  .company-name-logo > img {
    width: 20px;
    margin-right: 5px;
    border-radius: 2px;
  }

  .experience {
    border-radius: 5px;
    border: 1px solid rgb(108, 107, 107);
    width: max-content;
    padding: 5px;
    margin-right: 10px;
    background-color: #232733;
  }

  .name-intro {
    max-width: 600px;
    margin-bottom: 15px;
  }

  .name-intro > h1 {
    margin-bottom: 0px;
  }

  .name-intro > h3 {
    margin-top: 0px;
    font-weight: lighter;
  }

  .projects {
    text-align: left;
  }

  .projects > h3 {
    margin-bottom: 10px;
  }

  .worked-with {
    margin-top: 10px;
    font-weight: bold;
  }